/* stylelint-disable selector-pseudo-class-no-unknown, selector-max-universal */

@import './variable';
@import './font/sf-pro-display/sf-pro-display';

html,
body {
    background-color: #000;
    margin: 0;
    overscroll-behavior: none;
    padding: 0;
}

// Just for debugging
* {
    font-family: "Comic Sans MS", sans-serif;
}

html {
    overflow-y: scroll;
}

body {
    background-color: var(--main-background-color);
    font-family: sf-pro-display-black, sans-serif;
}
