%element,
.element {
    align-items: center;
    background-color: var(--main-background-color);
    border: 2px solid var(--main-border-color);
    border-radius: 12px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-grow: 0;
    gap: 4px;
    justify-content: center;
    padding: 12px 16px 8px 11px;
    user-select: none;
}

.element_text {
    color: var(--main-text-color);
    display: block;
    font-family: sf-pro-display-bold, sans-serif;
    font-size: 16px;
    line-height: 16px;
}

.element_to_move {
    @extend %element;

    left: 0;
    position: fixed;
    top: 0;
    user-select: none;
    z-index: -1000;

    &[data-is-ready-to-merge="true"] {
        background-color: #0c0;
    }
}

.element__crafted {
    @extend %element;

    left: 0;
    position: fixed;
    top: 0;
    transition: opacity 0.0001s linear;

    &[data-is-ready-to-merge="true"] {
        background-color: #00c;
    }
}
