/* stylelint-disable selector-pseudo-class-no-unknown, property-no-unknown */

[data-theme-name] {
    --main-text-color: #F4F4F7;
    --main-background-color: #161719;
    --main-border-color: #F4F4F7;
    --main-input-text-color: #FFF;
    --main-input-background-color: #000;
    --main-input-placeholer-color: #ABB1BB;

    // opposite
    --main-text-color-opposite: #161719;
    --main-background-color-opposite: #F4F4F7;
    --main-border-color-opposite: #161719;
    // non theme variables
    --main-top-button-margin: 16px;
    --main-padding: 16px;
}

[data-theme-name="dark"] {
    --main-text-color: #161719;
    --main-background-color: #FFF;
    --main-border-color: #161719;
    --main-input-text-color: #000;
    --main-input-background-color: #F4F4F7;
    --main-input-placeholer-color: #ABB1BB;

    // opposite
    --main-text-color-opposite: #fff;
    --main-background-color-opposite: #161719;
    --main-border-color-opposite: #fff;
}

// units and sizes
$ui-unit: 8px;
$max-width: 1200px;
$desktop-width: 1024px;
$tablet-width: 768px;
$phone-width: 480px;
$width-limiter-desktop-side-padding: $ui-unit * 3;
$width-limiter-tablet-side-padding: $ui-unit * 2;
$width-limiter-phone-side-padding: $ui-unit * 1.5;

:export {
    maxDesktopWidth: $desktop-width;
    maxPhoneWidth: $phone-width;
    maxTabletWidth: $tablet-width;
    widthLimiterDesktopSidePadding: $width-limiter-desktop-side-padding;
    widthLimiterPhoneSidePadding: $width-limiter-phone-side-padding;
    widthLimiterTabletSidePadding: $width-limiter-tablet-side-padding;
}

