.clear_element_field_button {
    background-image: url("/www/component/clear-element-field-button/image/clear-button-icon-light.svg");
    border: none;
    border-radius: 20px;
    display: block;
    flex-shrink: 0;
    height: 40px;
    margin: 0;
    padding: 0;
    width: 40px;
}

[data-theme-name="dark"] {
    .clear_element_field_button {
        background-image: url("/www/component/clear-element-field-button/image/clear-button-icon-dark.svg");
    }

}
