@import './../../css/variable';
@import './../../css/service';

.page {
    background-color: var(--main-background-color);
    color: var(--main-text-color);
    left: 0;
    line-height: 1.4;
    margin: 0;
    min-height: 100dvh;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

.page_children {
    @extend %width-limit;

    min-height: 100dvh;
}

