.element_base {
    background-color: var(--main-background-color);
    border-radius: 32px 32px 0 0;
    display: block;
    flex-grow: 1;
    padding: var(--main-padding);
}

.element_base__filter_wrapper {
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 24px;
    justify-content: center;
}

.element_base__filter_text_input__wrapper {
    flex-grow: 1;
    position: relative;
}

.element_base__filter_text_input {
    background-image: url("/www/component/element-base/image/search-icon-light.svg");
    background-position: 12px 10px;
    background-repeat: no-repeat;
    border: none;
    border-radius: var(--main-padding);
    box-sizing: border-box;
    display: block;
    font-size: 16px;
    line-height: 20px;
    padding: 9px 36px 7px 39px;
    width: 100%;
}

.element_base__filter_text_input__clean_search {
    background-image: url("/www/component/element-base/image/clean-search-icon-light.svg");
    background-position: center center;
    background-repeat: no-repeat;
    border-radius: 50%;
    display: block;
    height: 36px;
    position: absolute;
    right: 0;
    top: 0;
    width: 36px;
    z-index: 1;
}

.element_base__sort_button {
    background-image: url("/www/component/element-base/image/sort-icon-time-asc-light.svg");
    border: none;
    border-radius: 20px;
    box-sizing: border-box;
    display: block;
    flex-grow: 0;
    flex-shrink: 0;
    height: 40px;
    padding: 0;
    width: 40px;
}

.element_base__element_list {
    align-items: start;
    display: flex;
    flex-flow: row wrap;
    gap: 8px;
    height: 25dvh;
    margin: 0;
    overflow-y: auto;
    padding: var(--main-padding) 0;
    place-content: start start;
}

.element_base__element_list_item {
    display: block;
    margin: 0;
    padding: 0;
}

[data-theme-name="dark"] {
    .element_base__filter_text_input {
        background-image: url("/www/component/element-base/image/search-icon-dark.svg");
    }

    .element_base__sort_button {
        background-image: url("/www/component/element-base/image/sort-icon-time-asc-dark.svg");
    }

    .element_base__filter_text_input__clean_search {
        background-image: url("/www/component/element-base/image/clean-search-icon-dark.svg");
    }
}
