.element_counter {
    align-items: center;
    background-color: var(--main-background-color);
    border: 2px solid var(--main-border-color);
    border-radius: 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    gap: 4px;
    height: 40px;
    justify-content: center;
    padding: 0 2px 0 4px;
}

.element_counter__text {
    color: var(--main-text-color);
    font-family: sf-pro-display-bold, sans-serif;
    font-size: 16px;
    line-height: 16px;
    padding: 0 3px;
}

%element-counter-icon {
    background-position: center center;
    background-repeat: no-repeat;
    display: block;
    height: 28px;
    width: 28px;
}

.element_counter_subcounter {
    align-items: center;
    background-color: var(--main-background-color-opposite);
    border-radius: 16px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    gap: 4px;
    height: 32px;
    justify-content: center;
    padding: 2px 7px 2px 2px;

    .element_counter__text {
        color: var(--main-text-color-opposite);
    }
}

.element_counter__discovery {
    @extend %element-counter-icon;

    background-image: url("/www/component/element-counter/image/discovery-icon-light.svg");
}

.element_counter__recept {
    @extend %element-counter-icon;

    background-image: url("/www/component/element-counter/image/recept-icon-light.svg");
}

.element_counter__tag {
    @extend %element-counter-icon;

    background-image: url("/www/component/element-counter/image/tag-icon-light.svg");
}

[data-theme-name="dark"] {
    .element_counter__discovery {
        background-image: url("/www/component/element-counter/image/discovery-icon-dark.svg");
    }

    .element_counter__recept {
        background-image: url("/www/component/element-counter/image/recept-icon-dark.svg");
    }

    .element_counter__tag {
        background-image: url("/www/component/element-counter/image/tag-icon-dark.svg");
    }
}
