.prefer_color_scheme_button {
    background-image: url("/www/component/prefer-color-scheme-button/image/prefer-color-scheme-button-icon-light.svg");
    border: none;
    border-radius: 20px;
    display: block;
    height: 40px;
    width: 40px;
}

[data-theme-name="dark"] {
    .prefer_color_scheme_button {
        background-image: url("/www/component/prefer-color-scheme-button/image/prefer-color-scheme-button-icon-dark.svg");
    }
}
