@import "./../../../css/image/main-background/main-background";

.client_home {
    @extend %main-background;

    align-items: stretch;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100dvh;
}

.client_home__field_wrapper {
    display: flex;
    flex-direction: column;

    // background-color: #c00;
    flex-grow: 1;
}

.client_home__element_base_wrapper {
    display: flex;
    flex-direction: column;

    // background-color: #0c0;
    // flex-grow: 0.4;
    flex-shrink: 0;
}

// todo: move top buttons into left and right containers
.client_home__right_top,
.client_home__left_top {
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 8px;
    position: fixed;
    top: var(--main-top-button-margin);
}

.client_home__left_top {
    justify-content: end;
    left: var(--main-padding);
}

.client_home__right_top {
    justify-content: end;
    right: var(--main-padding);
}

